.search{
    position: relative;
    left: 4px;


    &-dropdown{
        &.filter-panel{
            .search-results{
                display: none;
            }   
        }

        &:not(.filter-panel){
            .filter-selection-group{
                display: none;
            }   
        }

        &-panel{
            .search-box-container{
                border: solid 1px $primaryBlue;
    
                .search-icon{
                    g{
                        stroke: $primaryBlue;
                    }
                }
            }
        }
    }

    // show/hide search dropdown panel
    &:not(.search-dropdown-panel){
        .search-dropdown{
         display: none;
        }
    }
 

    &-box-container{
        position: relative;
        background: #F2F2F2;
        border-radius: 20px;
        width: fit-content;
        height: 35px;
        min-width: 270px;
        gap:10px;
        padding: 0px 25px;

        >div{
            gap:10px;
        }
    }


    .filter-selection-group{
        >div{
            flex-wrap: wrap;
            gap:10px;
                .filter-check{
                    gap:5px;
                    cursor: pointer;
                    
                    >div{
                        position: relative;
                        width: 10px;
                        height:10px;
                        border-radius:2px;
                    }

                    p{
                        text-transform: uppercase;
                    }

                    svg{
                        display: none;
                    }

                    &.checked{
                        .check-mark{
                            display: block;
                            position: absolute;
                            width: 6px;
                            margin: auto;
                            inset: 0;
                            line-height: 0.9;

                            polyline{
                                stroke-width: 2px;
                            }
                        }
                    }
                }


            @include semiTransparentColouredBorderBox('.filter-check > div.red' , $primaryRed);
            @include semiTransparentColouredBorderBox('.filter-check > div.green' , $primaryGreen);
            @include semiTransparentColouredBorderBox('.filter-check > div.orange' , $primaryOrange);
            @include semiTransparentColouredBorderBox('.filter-check > div.purple' , $primaryPurple);
        }
    }


    .filtered{
        height: fit-content;

        p{
            border-bottom: solid 0.5px rgba($gray , 0.2);
            margin-bottom: 15px;
            padding-bottom: 5px;
            color: $gray;
        }

        >div{
            gap:5px;
        }

        .filter-item{
            @include tag;
            @include filteredTag('&.green' , $primaryGreen);
            @include filteredTag('&.red' , $primaryRed);
            @include filteredTag('&.orange' , $primaryOrange);
            @include filteredTag('&.purple' , $primaryPurple);
            height: 15px;
            color: $white;
        }

        svg{
            line{
                stroke: $white;
                stroke-width: 2px;
            }
        }
    }

    .filter-active{
        g{
            stroke:$primaryBlue;
        }
    }

    .no-results{
        padding-bottom: 10px;
        .search-input{
            color: $primaryRed;
            font-weight: 600;
        }
    }
}




.mobile-nav{
    
    &:not(.mobile-search){
        .search{
            display: none;
        }
    }

    &.mobile-search{
        // hide all side bar nav items
        .side-bar-logo ,
        .side-bar-mobile-nav-user-details,
        .side-bar-content{
            display: none;
        }

        .search{
            position: absolute;
            top:50px;
            width:  calc(100%  - 40px);
            max-width: 450px;
            left: 0;
            right: 0;
            margin: 0px auto;

            &-box-container{
                min-width:calc(100% - 40px);

                .search-icon{
                    display: none;
                }
            }

            .filtered{
                padding-bottom: 20px;
            }

            .search-dropdown{
                box-shadow: none !important;
                margin-top: 20px;
                padding: 0px 20px;
            }
        }
    }
}

// only have dropdown on top bar
.top-bar{
    .search-dropdown{
        position: absolute;
        left: -10px;
        top: -10px;
        width: calc(100% + 20px);
        background: $white;
        border-radius: 15px;
        padding-top: 65px;
        height: fit-content;
        z-index: -1;
        box-shadow: 8px 10px 15px rgba($gray , 0.2);

        .filtered{
            padding: 0px 20px 20px;
        }

        &-inner{
            position:relative;
            max-height: 300px;
            width: calc(100% - 40px);
            padding: 0px 20px 10px;
            overflow-y: scroll;

            &-group{
                padding-bottom: 15px;
            }

            .grayed{
                position: relative;

                &::after{
                    content: '';
                    position: absolute;
                    height: 100%;
                    width: calc(100% + 20px);
                    top:0;
                    left: -10px;
                    border-radius: 10px;
                    background: rgba($gray , 0.1);
                    z-index: -1;
                }
            }

            @include hideScroll('&')
        }
    }
}

.search-dropdown{
    .book{
        &-card{
            display: flex;
            width: 100%;
            gap:10px;
            height: 56px;
    
            &-details{
                >div{
                    display: flex;
                    flex-direction: column-reverse;
                }
            }
        
            &-image{
                height: 50px;
                width: 50px;
            }
    
            .book-author{
                display: block;
            }
    
            .book-description{
                display: none;
            }
        }
    }
}


// hide search bar in nav on tablet and desktop
@media only screen and (min-width: 924px){
    .side-bar{
        .search{
            display: none;
        }
    }
}